import React, { Suspense} from 'react';
import { Routes, Route } from 'react-router-dom';
import './scss/style.scss';
import Header from './components/Header/Header';
import Home from './pages/Home';
import Footer from './components/Footer/Footer';
import Fallback from './common/Fallback';
import NotFound from './components/NotFound';
const CatalogPage = React.lazy(() => import('./pages/CatalogPage'));
const ProductPage = React.lazy(() => import('./pages/ProductPage'));
const Form = React.lazy(() => import('./pages/Form'));

const App: React.FC = () => {
  return (
    <>
      <Header />
      <main className='page'>
        <Suspense fallback={<Fallback />}>
        <Routes>
          <Route path="nbhunter/" element={<Home />} />
          <Route path="nbhunter/product/:id" element={<ProductPage />} />
          <Route path="nbhunter/catalog" element={<CatalogPage />} />
          <Route path="nbhunter/jalobi-i-predlojeniya" element={<Form />} />
          <Route path="nbhunter/*" element={<NotFound />} />
          <Route path="nbhunter/notfound" element={<NotFound />} />
        </Routes>
        </Suspense>
      </main>
      <Footer />
    </>
  )
}

export default App;
